import React, { Component } from 'react';
import { Switch, BrowserRouter ,Route, Redirect } from 'react-router-dom'
import {MainLayout,EmptyLayout} from './layouts/main_layout';
import Main from "./components/main";
import Terminos from "./components/terminos_interlemd";
import createHistory from 'history/createBrowserHistory';


class App extends Component {
  render() {
    return (
      <BrowserRouter  history={createHistory} basename={process.env.PUBLIC_URL}>
      <Switch>   
      <Route exact path="/">
        <Redirect to="/terminos" />
      </Route>
      <EmptyLayout exact path="/terminos" component={Terminos}/>
      <MainLayout exact path="/home" component={Main}/>
      <Route component={() => (<div>404 Not found </div>)} />
      
      </Switch>
    </BrowserRouter>
    );
  }
}

export default App;
